<template>
  <div>
    <z-paging
      ref="pagingRef"
      v-model="dataList"
      @query="queryList"
      :loading-more-custom-style="{ 'padding-bottom': '200rpx' }"
    >
      <uv-sticky class="sticky">
        <view class="sticky-container">
          <view class="input-container">
            <listSearch
              :show="true"
              :disabled="false"
              @confirm="listHandleConfirm"
              @clear="listHandleClear"
              @map="handleMap"
            />
          </view>
          <instructorDropDown @search="search" ref="homeDropDownRef" />
        </view>
      </uv-sticky>
      <view class="main-box">
        <view v-for="(item, index) in dataList" :key="index" class="pb-32rpx">
          <instructorSiteItem
            :info="item"
            @toDetail="toDetail"
            :show-join="false"
            :page-show-quit="false"
          />
        </view>
        <view v-if="dataList.length == 0" style="min-height: 100vh">
          <empty></empty>
        </view>
      </view>
      <template #empty></template>
    </z-paging>
  </div>
</template>

<script>
import shareMixins from '@/mixins/shareMixins'
import listSearch from '@/components/SearchComponent/listSearch.vue'
import instructorSiteItem from '@/components/InstructorSiteItem/index.vue'
import instructorDropDown from './components/instructorDropDown.vue'
export default {
  mixins: [shareMixins],
  components: {
    instructorSiteItem,
    listSearch,
    instructorDropDown,
  },
  data() {
    return {
      area: '',
      list: [],
      urls: [],
      dataList: [],
      areaGroupList: [
        [
          {
            text: '全部区域',
            value: 0,
          },
        ],
      ],
      tempDataObj: {},
      searchObj: {},
      searchContent: '',
    }
  },
  onLoad(options) {
    this.queryList(1, 10)
  },
  methods: {
    handleMap() {
      if (this.$store.app.themeType == 1) {
        uni.navigateTo({
          url: '/pages/instructorSite/index',
        })
        return
      }
      uni.switchTab({ url: '/pages/tabbar/commonPage/index' })
    },
    toDetail(info) {
      uni.navigateTo({
        url: '/pages-sub/instructorSite/detail?instructor_site_id=' + info.instructor_site_id,
      })
    },
    listHandleConfirm(data) {
      this.searchContent = data
      this.queryList(1, 10)
    },
    listHandleClear() {
      this.searchContent = ''
      this.queryList(1, 10)
    },
    search(data) {
      this.searchObj = data
      this.queryList(1, 10)
    },

    async queryList(pageNo, pageSize) {
      let resAreaGroupList = this.tempDataObj.areaGroupList
      let params = {
        page: pageNo,
        size: pageSize,
        name: this.searchContent,
        ...this.searchObj,
      }
      let res = await this.$api.getInstructorSiteList(params)
      if (res.code !== 200) {
        this.$toast(res.message)
        return
      }
      this.$refs.pagingRef?.complete(res.data.list)
    },
  },
}
</script>

