swiper {
  height: 442rpx;
}

.sticky {
  top: -1px !important;
}
.sticky-container {
  background-color: #fff;
  padding-top: 16rpx;
}
.main-box {
  padding: 32rpx;
  box-sizing: border-box;
}
.input-container {
  margin-bottom: 16rpx;
}
